*{
    font-family: 'Poppins';
}
.mainwindow
{
display: grid;
grid-template-columns: 250px auto;
}
.mainFocus
{
    z-index:1;
}
.Header
{
    border-bottom: 1px solid grey;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-bottom: 15px;
    justify-content: space-between;
}
.tagline
{
    font-size: 2rem;
    font-weight: 800;
    color: rgb(34, 34, 75);
    margin-left: 10px;
}
.tagline>p
{
    font-size: 1rem;
}
.mainFocus
{
    height: 100vh;
    overflow-y: scroll;
    box-sizing:border-box;
}

