.form
{
    display: flex;
    flex-direction: row;
}
.form>*
{
    margin: 5px;   
}
.form>button
{
  width: 100px;
  border-radius: 10px;
  border: 1px solid white;
  color: white;
  background-color: blue;
}
.form>input
{
   width: 200px;
   border-radius: 10px;
   border: 1px solid grey;

}
.form>label
{
  padding: 5px 5px;
  font-size: 1.2rem;
  font-weight: 400;
}