

.LS
{
  margin-top: 30px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  width: 60vw;
  margin: auto;
  min-width:400px;
  /* border: 2px solid rgb(0, 0, 0); */
  border-radius: 10px;
  display: flex;
  padding: 15px;
  background-color:white;
  color: white;
  box-shadow: 5px 5px 15px black;
  flex-direction: column;
}
.button
{
    width: 40%;
    background-color:blueviolet;
    color:white;
    margin: auto;
    border:1px solid white;
    border-radius: 10px;
    font-size: 1.2rem;
    height: 35px;
}
.button:hover
{
    transform: scale(1.1);
}
.LS>input
{
    background-color: rgb(255, 255, 255);
    margin-bottom: 10px;
    height: 50px;
    width: 50%;
    font-size: 1.5rem;
    border: 1px solid rgb(0, 0, 0);
    color: rgb(0, 0, 0);
    /* border: 1px soid darkblue; */
}
.LS>label
{
    font-weight: 800;
    color: black ;
    /* border: 1px solid blue; */
}

.toggle
{
    margin: auto;
    width: max-content;
}
.togglebutton
{
    border: 1px solid blueviolet;
    color: white;
    background-color: blueviolet;
    font-size: 2rem;
    font-weight:100;
    border: 1px solid black;
}
.deselectedToggle
{
    background-color: white;
    color: black;
}
