.user
{
    border: 1px solid rgb(74, 74, 76);
    display: grid;
    grid-template-columns: 60px 100px auto 100px;
}
.user>*
{
  border-right: 1px solid grey;
}
.user>div>a>img
{
  width: 50px;
  height: 50px;  
}
.user>button
{
  color:black;
  background-color : white;
  height:45px;
  border: none;
  font-size: 1.2rem;
}
.user>button:hover
{
  transform: scale(0.9);
}
.personID
{
  padding:0px 5px ;
  text-overflow: ellipsis;
}